import { Plugin } from '@ali/4ever-cangjie';
import { isFoldInGroup, isUnfoldInGroup } from '@ali/4ever-plugin-heading-group';

function createShouldComponentUpdate(): Plugin['shouldNodeComponentUpdate'] {
  return function shouldNodeComponentUpdate(prevProps, nextProps, controller, next) {
    const { node: prevNode, document: prevDocument } = prevProps;
    const { node: nextNode, document: nextDocument } = nextProps;

    if (
      isFoldInGroup(prevDocument, prevNode, controller) &&
      isUnfoldInGroup(nextDocument, nextNode, controller)
    ) {
      return true;
    }

    return next();
  };
}

export default createShouldComponentUpdate;
